$darkMode: true

@mixin darkMode
    @if($darkMode)
        @media(prefers-color-scheme: dark)
            @content      

html
    background-color: whitesmoke
    color: dimgray
    font-family: "EB Garamond", serif
    font-size: 18px
    line-height: 1.3
    text-align: center
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale

    @include darkMode
        background-color: black
        color: whitesmoke

body
    max-width: 800px
    margin: 3em auto

a
    color: inherit
    text-decoration: underline
    text-decoration-color: darkred

a:hover
    color: darkred
    text-decoration: none

    @include darkMode
        color: inherit
        background-color: darkred

header
    a
        text-decoration: none

header, footer
    width: 100%
    margin: 3em auto

article
    padding: 2em
    border-bottom: 2px solid darkred

h1
    font-size: 2.5em
    line-height: 1.1
    margin: 0

h2
    font-size: .9em
    text-transform: uppercase

footer
    max-width: 40em
    margin: 3em auto
    font-size: 0.9em

article
    ul
        list-style-type: none
        padding: 0
        margin: 0

input
    padding: 0.8em
    font-size: 0.8em
    text-align: center
    width: 84%
    margin: 2em auto
    border-radius: 28px
    font-family: "Courier Prime", monospace
    font-weight: 700
    border: 3px solid darkred 
    background-color: white
    
    @include darkMode
        background-color: black
        color: whitesmoke

input:hover
    border: 3px solid dimgray
    background-color: darkred
    color: whitesmoke

    @include darkMode
        border: 3px solid whitesmoke

section
    max-height: 60vh
    overflow-y: scroll
    overflow-x: hidden
    margin: 2em auto
    scrollbar-width: thin
    scrollbar-color: darkred whitesmoke

    @include darkMode
        scrollbar-color: darkred black

